@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300;400;500;600;700&display=swap');

:root {
  /* Carbon Design System - IBM Colors */
  --carbon-black: #000000;
  --carbon-gray-100: #161616;
  --carbon-gray-90: #262626;
  --carbon-gray-80: #393939;
  --carbon-gray-70: #525252;
  --carbon-gray-60: #6f6f6f;
  --carbon-gray-50: #8d8d8d;
  --carbon-gray-40: #a8a8a8;
  --carbon-gray-30: #c6c6c6;
  --carbon-gray-20: #e0e0e0;
  --carbon-gray-10: #f4f4f4;
  --carbon-white: #ffffff;
  
  /* Green accent colors */
  --carbon-green-60: #24a148;
  --carbon-green-50: #42be65;
  --carbon-green-40: #6fdc8c;
  --carbon-green-30: #a7f0ba;
  --carbon-green-20: #defbe6;
  
  /* UI Colors */
  --primary-bg: var(--carbon-gray-100);
  --secondary-bg: var(--carbon-gray-90);
  --surface-bg: var(--carbon-gray-80);
  --accent: var(--carbon-green-50);
  --accent-hover: var(--carbon-green-40);
  --text-primary: var(--carbon-white);
  --text-secondary: var(--carbon-gray-30);
  --border-subtle: var(--carbon-gray-70);
  --border-strong: var(--carbon-gray-60);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'IBM Plex Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  background: var(--primary-bg);
  color: var(--text-primary);
  line-height: 1.6;
  overflow-x: hidden;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
}

/* Parallax Hero Section */
.parallax-hero {
  position: relative;
  min-height: 280px;
  overflow: hidden;
  border-bottom: 2px solid var(--accent);
}

.parallax-hero::before {
  content: '';
  position: absolute;
  top: -50px;
  left: 0;
  right: 0;
  bottom: -50px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: brightness(0.4) contrast(1.1);
  z-index: 0;
  will-change: transform;
}

.parallax-hero::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(180deg, rgba(0,0,0,0.5) 0%, var(--primary-bg) 95%);
  z-index: 1;
}

/* Header */
.app-header {
  position: relative;
  z-index: 10;
  padding: 2rem 2rem 2rem;
  background: transparent;
}

.header-content {
  max-width: 1400px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.app-title {
  font-size: clamp(2rem, 5vw, 3rem);
  font-weight: 300;
  letter-spacing: -0.02em;
  margin-bottom: 0.5rem;
  color: var(--carbon-white);
  text-shadow: 0 2px 20px rgba(0,0,0,0.8);
}

.app-title strong {
  font-weight: 600;
  color: var(--accent);
}

.app-subtitle {
  font-size: clamp(0.875rem, 2vw, 1rem);
  font-weight: 400;
  color: var(--carbon-gray-30);
  letter-spacing: 0.05em;
  margin-bottom: 2rem;
  text-shadow: 0 1px 10px rgba(0,0,0,0.8);
}

.app-header nav {
  display: flex;
  gap: 0;
  border-left: 2px solid var(--border-subtle);
  flex-wrap: wrap;
}

.app-header nav .nav-link {
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 400;
  font-size: 0.875rem;
  padding: 0.75rem 1.5rem;
  border-right: 1px solid var(--border-subtle);
  border-bottom: 2px solid transparent;
  transition: all 0.2s cubic-bezier(0.2, 0, 0.38, 0.9);
  background: var(--secondary-bg);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  white-space: nowrap;
}

.app-header nav .nav-link:hover {
  background: var(--surface-bg);
  border-bottom-color: var(--accent);
  color: var(--accent);
}

.app-header nav .nav-link.active {
  background: var(--surface-bg);
  border-bottom-color: var(--accent);
  color: var(--accent);
}

.app-header nav a {
  color: var(--text-primary);
  text-decoration: none;
  font-weight: 400;
  font-size: 0.875rem;
  padding: 0.75rem 1.5rem;
  border-right: 1px solid var(--border-subtle);
  border-bottom: 2px solid transparent;
  transition: all 0.2s cubic-bezier(0.2, 0, 0.38, 0.9);
  background: var(--secondary-bg);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  white-space: nowrap;
}

.app-header nav a:hover {
  background: var(--surface-bg);
  border-bottom-color: var(--accent);
  color: var(--accent);
}

/* Main Content */
.app-main {
  flex: 1;
  padding: 3rem 1rem;
  max-width: 100%;
  width: 100%;
}

.loading {
  text-align: center;
  padding: 4rem;
  font-size: 1rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: 300;
}

.loading::after {
  content: '...';
  animation: dots 1.5s infinite;
}

@keyframes dots {
  0%, 20% { content: '.'; }
  40% { content: '..'; }
  60%, 100% { content: '...'; }
}

/* Home Page Styles */
.home-container {
  max-width: 1400px;
  margin: 0 auto;
}

.home-intro {
  text-align: center;
  margin-bottom: 4rem;
}

.home-intro h1 {
  font-size: 2.5rem;
  font-weight: 300;
  color: var(--text-primary);
  margin-bottom: 1rem;
  letter-spacing: -0.02em;
}

.home-intro p {
  font-size: 1.25rem;
  color: var(--text-secondary);
  max-width: 700px;
  margin: 0 auto;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.service-card {
  background: var(--secondary-bg);
  border: 1px solid var(--border-subtle);
  padding: 2.5rem;
  text-decoration: none;
  color: var(--text-primary);
  transition: all 0.3s cubic-bezier(0.2, 0, 0.38, 0.9);
  position: relative;
  overflow: hidden;
}

.service-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--accent);
  transform: scaleX(0);
  transition: transform 0.3s;
}

.service-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 32px rgba(0,0,0,0.5);
  border-color: var(--accent);
}

.service-card:hover::before {
  transform: scaleX(1);
}

.service-icon {
  font-size: 3rem;
  margin-bottom: 1.5rem;
}

.service-card h2 {
  font-size: 1.75rem;
  font-weight: 400;
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.service-card p {
  color: var(--text-secondary);
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.service-features {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.service-features span {
  font-size: 0.9rem;
  color: var(--accent);
  display: flex;
  align-items: center;
}

.btn-view {
  display: inline-block;
  color: var(--accent);
  font-weight: 500;
  font-size: 0.875rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.info-section {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
  margin-top: 4rem;
}

.info-card {
  background: var(--surface-bg);
  padding: 2rem;
  border-left: 3px solid var(--accent);
  text-align: center;
}

.info-card h3 {
  font-size: 1.25rem;
  font-weight: 400;
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.info-card p {
  color: var(--text-secondary);
  line-height: 1.6;
  font-size: 0.95rem;
}

/* Page Header */
.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
  padding: 0 1rem;
  flex-wrap: wrap;
  gap: 1rem;
}

.page-header h1 {
  font-size: 2rem;
  font-weight: 300;
  color: var(--text-primary);
}

.btn-normativa,
.btn-back {
  background: var(--surface-bg);
  border: 1px solid var(--border-strong);
  color: var(--text-primary);
  padding: 0.75rem 1.5rem;
  font-size: 0.875rem;
  font-family: 'IBM Plex Sans', sans-serif;
  cursor: pointer;
  transition: all 0.15s;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 500;
}

.btn-normativa:hover,
.btn-back:hover {
  background: var(--accent);
  border-color: var(--accent);
  color: var(--carbon-black);
}

/* Header Buttons */
.header-buttons {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}

/* Padel Info Section */
.padel-info {
  padding: 0 1rem;
  margin-bottom: 2rem;
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;
}

/* Calendar Container */
.calendar-container {
  background: var(--secondary-bg);
  border: 1px solid var(--border-subtle);
  padding: 0;
  box-shadow: 0 4px 24px rgba(0,0,0,0.4);
  max-width: 1600px;
  margin: 0 auto;
}

.calendar-header-section {
  padding: 1.5rem;
  border-bottom: 1px solid var(--border-subtle);
  background: var(--primary-bg);
}

.calendar-header-section h2 {
  font-size: clamp(1.25rem, 3vw, 1.5rem);
  font-weight: 300;
  color: var(--text-primary);
  letter-spacing: 0.02em;
  margin: 0 0 1rem 0;
}

.calendar-controls {
  display: flex;
  gap: 0.75rem;
  align-items: center;
  flex-wrap: wrap;
}

.calendar-controls button {
  background: var(--surface-bg);
  border: 1px solid var(--border-strong);
  color: var(--text-primary);
  padding: 0.625rem 1rem;
  font-size: 0.75rem;
  font-family: 'IBM Plex Sans', sans-serif;
  cursor: pointer;
  transition: all 0.15s;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 500;
  white-space: nowrap;
}

.calendar-controls button:hover:not(:disabled) {
  background: var(--accent);
  border-color: var(--accent);
  color: var(--carbon-black);
}

.calendar-controls button:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.calendar-grid-wrapper {
  padding: 1rem;
  overflow-x: auto;
  overflow-y: visible;
}

/* Calendar Grid - Ajustado para 8 columnas (horario + 7 días) */
.calendar-grid {
  width: 100%;
  min-width: 1000px;
}

.calendar-header {
  display: grid;
  grid-template-columns: 100px repeat(7, 1fr);
  gap: 1px;
  margin-bottom: 1px;
  background: var(--border-subtle);
}

.time-column-header {
  background: var(--primary-bg);
  padding: 0.75rem;
  font-weight: 600;
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: var(--text-secondary);
  display: flex;
  align-items: center;
  justify-content: center;
}

.date-column {
  background: var(--surface-bg);
  color: var(--text-primary);
  padding: 0.75rem 0.5rem;
  text-align: center;
  border-left: 2px solid transparent;
  transition: all 0.2s;
  min-width: 0;
}

.date-column:hover {
  border-left-color: var(--accent);
}

.day-name {
  font-size: 0.65rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: var(--text-secondary);
  margin-bottom: 0.25rem;
}

.date-info {
  font-size: 1.5rem;
  font-weight: 300;
  color: var(--text-primary);
  line-height: 1;
}

.date-month-year {
  font-size: 0.625rem;
  color: var(--text-secondary);
  margin-top: 0.25rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.calendar-row {
  display: grid;
  grid-template-columns: 100px repeat(7, 1fr);
  gap: 1px;
  margin-bottom: 1px;
  background: var(--border-subtle);
}

.time-cell {
  background: var(--primary-bg);
  padding: 1rem 0.5rem;
  font-weight: 500;
  font-size: 0.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--accent);
  letter-spacing: 0.05em;
  line-height: 1.3;
}

.time-cell .time-end {
  font-size: 0.7rem;
  color: var(--text-secondary);
  font-weight: 400;
  margin-top: 0.15rem;
}

.slot-cell {
  background: var(--surface-bg);
  border-left: 2px solid transparent;
  padding: 0.75rem 0.5rem;
  min-height: 90px;
  cursor: pointer;
  transition: all 0.2s cubic-bezier(0.2, 0, 0.38, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  position: relative;
  overflow: hidden;
  min-width: 0;
}

.slot-cell::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--accent);
  opacity: 0;
  transition: opacity 0.2s;
  z-index: 0;
}

.slot-cell.available:hover::before {
  opacity: 0.1;
}

.slot-cell.available {
  border-left-color: var(--carbon-green-40);
}

.slot-cell.available:hover {
  background: var(--carbon-gray-70);
  transform: translateY(-2px);
  box-shadow: inset 0 0 0 1px var(--accent);
}

.slot-cell.occupied {
  background: var(--carbon-gray-90);
  border-left-color: var(--carbon-gray-60);
  cursor: default;
}

.available-label {
  color: var(--accent);
  font-weight: 500;
  font-size: 0.7rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  position: relative;
  z-index: 1;
}

.reservation-info {
  font-size: 0.75rem;
  position: relative;
  z-index: 1;
  max-width: 100%;
  width: 100%;
  padding: 0 0.25rem;
}

.reservation-info .name {
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: block;
}

.reservation-info .location {
  color: var(--text-secondary);
  font-size: 0.65rem;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  white-space: nowrap;
}

/* Local Calendar List View */
.local-calendar-list {
  max-width: 1400px;
  margin: 0 auto;
}

.calendar-section {
  margin-bottom: 3rem;
  margin-top: 2rem;
}

.section-header {
  padding: 1.5rem;
  border-left: 4px solid var(--accent);
  margin-bottom: 2rem;
}

.section-header.available-header {
  background: linear-gradient(90deg, var(--surface-bg) 0%, var(--secondary-bg) 100%);
  border-left-color: var(--accent);
}

.section-header.reserved-header {
  background: linear-gradient(90deg, var(--carbon-gray-80) 0%, var(--secondary-bg) 100%);
  border-left-color: var(--carbon-gray-60);
}

.section-header h3 {
  font-size: 1.5rem;
  font-weight: 400;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.section-subtitle {
  font-size: 0.875rem;
  color: var(--text-secondary);
  margin: 0;
}

.dates-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 1.5rem;
  padding: 0 1rem;
}

.date-card {
  background: var(--surface-bg);
  border: 1px solid var(--border-subtle);
  padding: 1.5rem;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.2, 0, 0.38, 0.9);
  position: relative;
  overflow: hidden;
}

.date-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 4px;
  height: 100%;
  background: var(--accent);
  transform: scaleY(0);
  transition: transform 0.3s;
}

.date-card.available:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 24px rgba(0,0,0,0.5);
  border-color: var(--accent);
}

.date-card.available:hover::before {
  transform: scaleY(1);
}

.date-card.reserved {
  background: var(--carbon-gray-90);
  border-color: var(--carbon-gray-70);
  opacity: 0.9;
}

.date-card.reserved::before {
  background: var(--carbon-gray-60);
}

.date-card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 1rem;
}

.day-number {
  font-size: 3rem;
  font-weight: 300;
  line-height: 1;
  color: var(--accent);
  transition: color 0.3s;
}

.day-number.reserved-number {
  color: var(--carbon-gray-50);
}

.status-badge {
  padding: 0.35rem 0.75rem;
  font-size: 0.7rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-radius: 2px;
}

.status-badge.available {
  background: var(--accent);
  color: var(--carbon-black);
}

.status-badge.reserved {
  background: var(--carbon-gray-70);
  color: var(--carbon-gray-30);
}

.date-card-body {
  margin-bottom: 1.5rem;
}

.date-card-body .day-name {
  font-size: 1.1rem;
  font-weight: 500;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
  text-transform: none;
  letter-spacing: normal;
}

.date-card.reserved .day-name {
  color: var(--text-secondary);
}

.date-full {
  font-size: 0.875rem;
  color: var(--text-secondary);
}

.date-card-footer {
  border-top: 1px solid var(--border-subtle);
  padding-top: 1rem;
  min-height: 60px;
}

.date-card.reserved .date-card-footer {
  border-top-color: var(--carbon-gray-80);
}

.btn-reserve {
  width: 100%;
  background: transparent;
  border: 1px solid var(--accent);
  color: var(--accent);
  padding: 0.75rem;
  font-size: 0.875rem;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  cursor: pointer;
  transition: all 0.2s;
  font-family: 'IBM Plex Sans', sans-serif;
}

.btn-reserve:hover {
  background: var(--accent);
  color: var(--carbon-black);
}

.reserved-info {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.reserved-name {
  font-size: 0.95rem;
  font-weight: 600;
  color: var(--text-primary);
}

.reserved-location {
  font-size: 0.8rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.reserved-motivo {
  font-size: 0.85rem;
  color: var(--text-secondary);
  font-style: italic;
  margin-top: 0.75rem;
  padding-top: 0.75rem;
  border-top: 1px solid var(--carbon-gray-80);
  line-height: 1.4;
}

/* Reservations List */
.reservations-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 0 1rem;
}

.reservation-card {
  background: var(--surface-bg);
  border: 1px solid var(--border-subtle);
  padding: 1.5rem;
  display: flex;
  gap: 1.5rem;
  align-items: center;
  position: relative;
}

.reservation-date-block {
  flex-shrink: 0;
  width: 70px;
  text-align: center;
  padding: 0.75rem;
  background: var(--primary-bg);
  border: 1px solid var(--border-strong);
}

.res-day-number {
  font-size: 2rem;
  font-weight: 300;
  color: var(--text-primary);
  line-height: 1;
}

.res-month {
  font-size: 0.75rem;
  text-transform: uppercase;
  color: var(--text-secondary);
  margin-top: 0.25rem;
}

.reservation-details {
  flex: 1;
}

.res-day-name {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
  margin-bottom: 0.25rem;
}

.res-full-date {
  font-size: 1.1rem;
  font-weight: 400;
  color: var(--text-primary);
  margin-bottom: 0.75rem;
}

.res-user {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  margin-bottom: 0.5rem;
}

.res-user strong {
  color: var(--text-primary);
  font-size: 0.95rem;
}

.res-location {
  font-size: 0.8rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.res-motivo {
  font-size: 0.875rem;
  color: var(--text-secondary);
  font-style: italic;
  margin-top: 0.5rem;
  padding-left: 1rem;
  border-left: 2px solid var(--border-subtle);
}

/* Info Box */
.calendar-info {
  padding: 0 1rem;
  margin-bottom: 2rem;
}

.info-box {
  background: var(--surface-bg);
  border-left: 3px solid var(--accent);
  padding: 1.5rem;
  color: var(--text-primary);
}

.info-box strong {
  display: block;
  margin-bottom: 1rem;
  font-size: 1rem;
  color: var(--accent);
}

.info-box ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.info-box li {
  padding: 0.5rem 0;
  padding-left: 1.5rem;
  position: relative;
  color: var(--text-secondary);
  line-height: 1.6;
}

.info-box li::before {
  content: '→';
  position: absolute;
  left: 0;
  color: var(--accent);
  font-weight: bold;
}

.empty-state {
  text-align: center;
  padding: 4rem 2rem;
  color: var(--text-secondary);
}

.empty-icon {
  font-size: 4rem;
  display: block;
  margin-bottom: 1rem;
  opacity: 0.5;
}

.empty-state p {
  font-size: 1.1rem;
}

/* Historico Styles */
.historico-container {
  background: var(--secondary-bg);
  border: 1px solid var(--border-subtle);
  padding: 0;
  box-shadow: 0 4px 24px rgba(0,0,0,0.4);
  max-width: 1200px;
  margin: 0 auto;
}

.historico-header {
  padding: 1.5rem;
  border-bottom: 1px solid var(--border-subtle);
  background: var(--primary-bg);
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.historico-header h2 {
  font-size: 1.5rem;
  font-weight: 300;
  color: var(--text-primary);
  margin: 0;
}

.historico-controls {
  display: flex;
  gap: 0.75rem;
  align-items: center;
  flex-wrap: wrap;
}

.historico-controls button {
  background: var(--surface-bg);
  border: 1px solid var(--border-strong);
  color: var(--text-primary);
  padding: 0.625rem 1rem;
  font-size: 0.75rem;
  font-family: 'IBM Plex Sans', sans-serif;
  cursor: pointer;
  transition: all 0.15s;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: 500;
  white-space: nowrap;
}

.historico-controls button:hover:not(:disabled) {
  background: var(--accent);
  border-color: var(--accent);
  color: var(--carbon-black);
}

.historico-controls button:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.historico-month-title {
  padding: 1.5rem;
  background: var(--surface-bg);
  border-bottom: 1px solid var(--border-subtle);
  border-left: 4px solid var(--accent);
}

.historico-month-title h3 {
  font-size: 1.75rem;
  font-weight: 300;
  color: var(--text-primary);
  margin: 0 0 0.5rem 0;
}

.historico-count {
  font-size: 0.875rem;
  color: var(--text-secondary);
  margin: 0;
}

.historico-list {
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.historico-item {
  background: var(--surface-bg);
  border: 1px solid var(--border-subtle);
  border-left: 3px solid var(--accent);
  padding: 1.5rem;
  display: flex;
  gap: 1.5rem;
  align-items: center;
  transition: all 0.2s;
}

.historico-item:hover {
  background: var(--carbon-gray-70);
  border-left-width: 4px;
}

.historico-item.cancelada {
  border-left-color: var(--carbon-gray-60);
  opacity: 0.7;
}

.historico-date {
  flex-shrink: 0;
  width: 70px;
  text-align: center;
  padding: 0.75rem;
  background: var(--primary-bg);
  border: 1px solid var(--border-strong);
}

.historico-day {
  font-size: 2rem;
  font-weight: 300;
  color: var(--accent);
  line-height: 1;
}

.historico-item.cancelada .historico-day {
  color: var(--carbon-gray-60);
}

.historico-month {
  font-size: 0.75rem;
  text-transform: uppercase;
  color: var(--text-secondary);
  margin-top: 0.25rem;
  letter-spacing: 0.05em;
}

.historico-year {
  font-size: 0.7rem;
  color: var(--text-secondary);
  margin-top: 0.15rem;
}

.historico-details {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.historico-time {
  font-size: 1.1rem;
  color: var(--text-primary);
  margin-bottom: 0.25rem;
}

.historico-time strong {
  color: var(--accent);
  font-weight: 600;
}

.historico-item.cancelada .historico-time strong {
  color: var(--carbon-gray-60);
}

.historico-user {
  font-size: 0.95rem;
  color: var(--text-primary);
}

.historico-user strong {
  font-weight: 600;
}

.historico-location {
  font-size: 0.8rem;
  color: var(--text-secondary);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.historico-motivo {
  font-size: 0.875rem;
  color: var(--text-secondary);
  font-style: italic;
  margin-top: 0.5rem;
  padding-top: 0.5rem;
  border-top: 1px solid var(--border-subtle);
  line-height: 1.4;
}

.historico-status {
  flex-shrink: 0;
  padding: 0.5rem 1rem;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-radius: 2px;
}

.historico-status.activa {
  background: var(--accent);
  color: var(--carbon-black);
}

.historico-status.cancelada {
  background: var(--carbon-gray-70);
  color: var(--carbon-gray-30);
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.85);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
  animation: fadeIn 0.2s;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

.reservation-form-container {
  background: var(--secondary-bg);
  border: 1px solid var(--border-strong);
  padding: 0;
  max-width: 600px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 8px 48px rgba(0,0,0,0.6);
  animation: slideUp 0.3s cubic-bezier(0.2, 0, 0.38, 0.9);
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.form-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid var(--border-subtle);
  background: var(--primary-bg);
}

.form-header h2 {
  color: var(--text-primary);
  font-size: 1.25rem;
  font-weight: 300;
  letter-spacing: 0.02em;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: var(--text-secondary);
  padding: 0.5rem;
  line-height: 1;
  transition: color 0.2s;
}

.close-btn:hover {
  color: var(--accent);
}

.selected-slot-info {
  background: var(--surface-bg);
  padding: 1.5rem 2rem;
  border-bottom: 1px solid var(--border-subtle);
  border-left: 2px solid var(--accent);
}

.selected-slot-info p {
  margin: 0.5rem 0;
  color: var(--text-primary);
  font-size: 0.875rem;
}

.selected-slot-info strong {
  color: var(--accent);
  font-weight: 500;
}

.reservation-form {
  padding: 2rem;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 500;
  color: var(--text-secondary);
  font-size: 0.75rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border: none;
  border-bottom: 1px solid var(--border-strong);
  background: var(--surface-bg);
  color: var(--text-primary);
  font-size: 1rem;
  font-family: 'IBM Plex Sans', sans-serif;
  transition: all 0.2s;
}

.form-group textarea {
  resize: vertical;
  min-height: 80px;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-bottom-color: var(--accent);
  background: var(--carbon-gray-70);
}

.form-actions {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--border-subtle);
}

.btn-cancel,
.btn-submit {
  flex: 1;
  padding: 1rem;
  border: 1px solid var(--border-strong);
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.15s;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-family: 'IBM Plex Sans', sans-serif;
}

.btn-cancel {
  background: transparent;
  color: var(--text-primary);
}

.btn-cancel:hover {
  background: var(--surface-bg);
  border-color: var(--text-primary);
}

.btn-submit {
  background: var(--accent);
  color: var(--carbon-black);
  border-color: var(--accent);
}

.btn-submit:hover:not(:disabled) {
  background: var(--accent-hover);
  border-color: var(--accent-hover);
}

.btn-submit:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

/* Normativa */
.normativa-container {
  background: var(--secondary-bg);
  border: 1px solid var(--border-subtle);
  padding: 2rem;
  max-width: 1000px;
  margin: 0 auto;
  box-shadow: 0 4px 24px rgba(0,0,0,0.4);
}

.normativa-container h1 {
  color: var(--accent);
  margin-bottom: 2rem;
  font-size: clamp(1.5rem, 4vw, 2rem);
  font-weight: 300;
  letter-spacing: 0.02em;
  text-align: center;
  border-bottom: 1px solid var(--border-subtle);
  padding-bottom: 1rem;
}

.normativa-container h2 {
  color: var(--accent);
  font-size: 1.5rem;
  font-weight: 400;
  margin: 2.5rem 0 1.5rem 0;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid var(--border-subtle);
}

.normativa-list {
  list-style: none;
  counter-reset: item;
  line-height: 1.8;
}

.normativa-list li {
  margin-bottom: 1.5rem;
  padding-left: 3rem;
  position: relative;
  counter-increment: item;
  color: var(--text-primary);
}

.normativa-list li::before {
  content: counter(item, decimal-leading-zero);
  position: absolute;
  left: 0;
  top: 0;
  font-weight: 600;
  color: var(--accent);
  font-size: 1.25rem;
  font-variant-numeric: tabular-nums;
}

/* Cancel Reservation */
.cancel-container {
  background: var(--secondary-bg);
  border: 1px solid var(--border-subtle);
  padding: 2rem;
  max-width: 600px;
  margin: 0 auto;
  box-shadow: 0 4px 24px rgba(0,0,0,0.4);
}

.cancel-container h2 {
  color: var(--accent);
  margin-bottom: 2rem;
  font-size: 1.75rem;
  font-weight: 300;
  text-align: center;
}

.message {
  padding: 1.5rem;
  margin-bottom: 2rem;
  text-align: center;
  font-weight: 500;
  border-left: 3px solid;
  background: var(--surface-bg);
}

.message.success {
  border-color: var(--accent);
  color: var(--accent);
}

.message.error {
  border-color: #fa4d56;
  color: #fa4d56;
}

.reservation-details {
  background: var(--surface-bg);
  padding: 2rem;
  border-left: 2px solid var(--accent);
  margin-bottom: 2rem;
}

.reservation-details h3 {
  color: var(--accent);
  margin-bottom: 1.5rem;
  font-weight: 400;
  font-size: 1.1rem;
}

.reservation-details p {
  margin: 1rem 0;
  font-size: 1rem;
  color: var(--text-primary);
}

.reservation-details strong {
  color: var(--text-secondary);
  font-weight: 500;
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.1em;
  display: inline-block;
  min-width: 100px;
}

.cancel-actions {
  margin-top: 2rem;
  text-align: center;
}

.btn-cancel-confirm {
  background: #fa4d56;
  color: var(--carbon-white);
  border: 1px solid #fa4d56;
  padding: 1rem 2rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.15s;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-family: 'IBM Plex Sans', sans-serif;
}

.btn-cancel-confirm:hover:not(:disabled) {
  background: #da1e28;
  border-color: #da1e28;
}

.btn-cancel-confirm:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

/* Footer */
.app-footer {
  background: var(--carbon-black);
  color: var(--text-secondary);
  text-align: center;
  padding: 2rem 1rem;
  margin-top: 4rem;
  border-top: 1px solid var(--border-subtle);
  font-size: 0.875rem;
  letter-spacing: 0.05em;
}

.app-footer strong {
  color: var(--accent);
  font-weight: 500;
}

/* Responsive Mobile Improvements */
@media (max-width: 768px) {
  .parallax-hero {
    min-height: 160px;
