  .parallax-hero::before {
    background-attachment: scroll;
    background-position: center 40%;
  }

  .app-header {
    padding: 1rem;
  }

  .app-title {
    font-size: 1.75rem;
    margin-bottom: 0.25rem;
  }

  .app-subtitle {
    font-size: 0.75rem;
    margin-bottom: 1rem;
  }

  .app-main {
