  }

  .reservation-info .location {
    font-size: 0.6rem;
  }

  /* Local calendar list mobile */
  .dates-grid {
    grid-template-columns: 1fr;
    padding: 0 0.5rem;
  }

  .reservation-card {
    flex-direction: column;
    align-items: flex-start;
  }

  .reservation-date-block {
